import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.map.js";
import axios from 'axios';
import { mapGetters } from 'vuex';
export default {
  name: 'PublicBrowse',
  data() {
    return {
      files: [],
      allowedDirs: [],
      rootDir: '',
      currentPath: '',
      totalCount: 0,
      loading: false,
      error: null,
      canRetry: true,
      hasMore: true,
      previewVisible: false,
      previewIndex: 0,
      observer: null,
      pageSize: 24,
      columnCount: 4,
      columnHeights: [0, 0, 0, 0],
      // 滑动相关
      touchStartX: 0,
      touchStartY: 0,
      touchCurrentX: 0,
      isSwiping: false,
      swipeOffset: 0,
      isAnimating: false,
      justTouched: false
    };
  },
  computed: {
    ...mapGetters(['userConfig']),
    siteName() {
      return this.userConfig?.siteTitle || '公开相册';
    },
    rootDirName() {
      return this.rootDir.split('/').filter(Boolean).pop() || '根目录';
    },
    pathParts() {
      if (!this.currentPath || !this.rootDir) return [];
      const relative = this.currentPath.replace(this.rootDir, '').replace(/^\/+/, '');
      return relative.split('/').filter(Boolean);
    },
    folders() {
      return this.files.filter(f => f.isFolder);
    },
    mediaFiles() {
      return this.files.filter(f => !f.isFolder);
    },
    columns() {
      const cols = Array.from({
        length: this.columnCount
      }, () => []);
      for (const file of this.mediaFiles) {
        const idx = file.columnIndex ?? 0;
        if (idx < this.columnCount) {
          cols[idx].push(file);
        } else {
          cols[0].push(file);
        }
      }
      return cols;
    },
    currentPreviewFile() {
      return this.mediaFiles[this.previewIndex];
    },
    swipeTrackStyle() {
      const offset = this.isSwiping ? this.swipeOffset : 0;
      // 基础位置是 -33.333%（显示中间的当前图片）
      return {
        transform: `translateX(calc(-33.333% + ${offset}px))`,
        transition: this.isSwiping ? 'none' : 'transform 0.35s cubic-bezier(0.25, 0.46, 0.45, 0.94)'
      };
    }
  },
  watch: {
    '$route.params.dir': {
      handler() {
        this.initFromRoute();
      }
    }
  },
  mounted() {
    this.initFromRoute();
    this.setupIntersectionObserver();
    this.updateColumnCount();
    window.addEventListener('resize', this.updateColumnCount);
  },
  beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect();
    }
    window.removeEventListener('resize', this.updateColumnCount);
  },
  methods: {
    updateColumnCount() {
      const width = window.innerWidth;
      let newCount;
      if (width < 600) {
        newCount = 2;
      } else if (width < 900) {
        newCount = 3;
      } else {
        newCount = 4;
      }
      if (newCount !== this.columnCount) {
        this.columnCount = newCount;
        this.columnHeights = new Array(this.columnCount).fill(0);
        // 重新分配所有文件到列
        this.mediaFiles.forEach(f => {
          f.columnIndex = undefined;
          this.assignToColumn(f);
        });
      }
    },
    getShortestColumn() {
      let minIndex = 0;
      let minHeight = this.columnHeights[0];
      for (let i = 1; i < this.columnCount; i++) {
        if (this.columnHeights[i] < minHeight) {
          minHeight = this.columnHeights[i];
          minIndex = i;
        }
      }
      return minIndex;
    },
    assignToColumn(file, height = 200) {
      const colIndex = this.getShortestColumn();
      file.columnIndex = colIndex;
      this.columnHeights[colIndex] += height;
    },
    onImageLoad(event, file) {
      const img = event.target;
      const ratio = img.naturalHeight / img.naturalWidth;
      const height = 280 * ratio;
      if (file.columnIndex === undefined) {
        this.assignToColumn(file, height);
      }
      file.loaded = true;
    },
    onVideoLoad(event, file) {
      const video = event.target;
      const ratio = video.videoHeight / video.videoWidth;
      const height = 280 * ratio;
      if (file.columnIndex === undefined) {
        this.assignToColumn(file, height);
      }
      file.loaded = true;
    },
    setupIntersectionObserver() {
      this.observer = new IntersectionObserver(entries => {
        const entry = entries[0];
        if (entry.isIntersecting && this.hasMore && !this.loading) {
          this.loadMore();
        }
      }, {
        rootMargin: '200px'
      });
    },
    observeLoadTrigger() {
      this.$nextTick(() => {
        if (this.$refs.loadTrigger && this.observer) {
          this.observer.observe(this.$refs.loadTrigger);
        }
      });
    },
    async initFromRoute() {
      const dirParam = this.$route.params.dir || '';
      const dirPath = Array.isArray(dirParam) ? dirParam.join('/') : dirParam;
      if (!dirPath) {
        this.error = '请指定要浏览的目录，例如: /browse/landscape';
        this.canRetry = false;
        return;
      }
      const parts = dirPath.split('/').filter(Boolean);
      this.rootDir = parts[0];
      this.currentPath = dirPath;
      this.files = [];
      this.hasMore = true;
      this.columnHeights = new Array(this.columnCount).fill(0);
      await this.loadFiles();
      this.observeLoadTrigger();
    },
    async loadFiles() {
      this.loading = true;
      this.error = null;
      this.canRetry = true;
      try {
        const res = await axios.get(`/api/public/list?dir=${encodeURIComponent(this.currentPath)}&count=${this.pageSize}`);
        if (res.data.allowedDirs) {
          this.allowedDirs = res.data.allowedDirs;
        }
        const dirs = (res.data.directories || []).map(d => ({
          name: d,
          isFolder: true
        }));
        const files = (res.data.files || []).map(f => ({
          name: f.name,
          isFolder: false,
          metadata: f.metadata,
          columnIndex: undefined
        }));

        // 预分配列
        files.forEach(f => this.assignToColumn(f));
        this.files = [...dirs, ...files];
        this.totalCount = res.data.totalCount || this.files.length;
        this.hasMore = this.mediaFiles.length < this.totalCount;
      } catch (err) {
        if (err.response?.status === 403) {
          const msg = err.response?.data?.error || '';
          if (msg.includes('disabled')) {
            this.error = '公开浏览功能未启用';
          } else if (msg.includes('not allowed') || msg.includes('No public')) {
            this.error = '该目录不允许公开访问';
          } else {
            this.error = '访问被拒绝';
          }
          this.canRetry = false;
        } else {
          this.error = '加载失败，请重试';
        }
      } finally {
        this.loading = false;
      }
    },
    async loadMore() {
      if (this.loading || !this.hasMore) return;
      this.loading = true;
      try {
        const start = this.mediaFiles.length;
        const res = await axios.get(`/api/public/list?dir=${encodeURIComponent(this.currentPath)}&start=${start}&count=${this.pageSize}`);
        const moreFiles = (res.data.files || []).map(f => ({
          name: f.name,
          isFolder: false,
          metadata: f.metadata,
          columnIndex: undefined
        }));
        moreFiles.forEach(f => this.assignToColumn(f));
        this.files.push(...moreFiles);
        this.hasMore = this.mediaFiles.length < this.totalCount;
      } catch (err) {
        console.error('加载更多失败', err);
      } finally {
        this.loading = false;
      }
    },
    enterFolder(folderPath) {
      const newPath = folderPath.replace(/\/+$/, '');
      this.$router.push(`/browse/${newPath}`);
    },
    goToRoot() {
      this.$router.push(`/browse/${this.rootDir}`);
    },
    goToPath(index) {
      const parts = this.pathParts.slice(0, index + 1);
      const newPath = this.rootDir + (parts.length ? '/' + parts.join('/') : '');
      this.$router.push(`/browse/${newPath}`);
    },
    getFolderName(path) {
      return path.split('/').filter(Boolean).pop() || path;
    },
    getFileUrl(name) {
      return `${window.location.origin}/file/${name}`;
    },
    isImage(file) {
      const ext = file.name.split('.').pop().toLowerCase();
      return ['jpg', 'jpeg', 'png', 'gif', 'webp', 'bmp', 'svg'].includes(ext);
    },
    isVideo(file) {
      const ext = file.name.split('.').pop().toLowerCase();
      return ['mp4', 'webm', 'ogg', 'mov'].includes(ext);
    },
    handleImageError(e) {
      e.target.style.display = 'none';
    },
    copyLink(name) {
      const url = this.getFileUrl(name);
      navigator.clipboard?.writeText(url).then(() => {
        this.showToast('已复制');
      }).catch(() => {
        // 降级方案
        const input = document.createElement('input');
        input.value = url;
        document.body.appendChild(input);
        input.select();
        document.execCommand('copy');
        document.body.removeChild(input);
        this.showToast('已复制');
      });
    },
    showToast(msg) {
      const existing = document.querySelector('.copy-toast');
      if (existing) existing.remove();
      const toast = document.createElement('div');
      toast.className = 'copy-toast';
      toast.textContent = msg;
      document.body.appendChild(toast);
      setTimeout(() => toast.classList.add('show'), 10);
      setTimeout(() => {
        toast.classList.remove('show');
        setTimeout(() => toast.remove(), 300);
      }, 1500);
    },
    downloadFile(name) {
      const link = document.createElement('a');
      link.href = this.getFileUrl(name);
      link.download = name.split('/').pop();
      link.click();
    },
    openPreview(file) {
      if (file.isFolder) return;
      const mediaIndex = this.mediaFiles.findIndex(f => f.name === file.name);
      if (mediaIndex >= 0) {
        this.previewIndex = mediaIndex;
        this.previewVisible = true;
        document.body.style.overflow = 'hidden';
      }
    },
    closePreview() {
      this.previewVisible = false;
      document.body.style.overflow = '';
    },
    prevImage() {
      if (this.previewIndex > 0) {
        this.previewIndex--;
      }
    },
    nextImage() {
      if (this.previewIndex < this.mediaFiles.length - 1) {
        this.previewIndex++;
      }
    },
    handleModalClick(e) {
      // 如果刚刚触摸过，忽略这次点击（防止触摸结束后触发click）
      if (this.justTouched) return;
      this.closePreview();
    },
    // 触摸滑动事件
    onTouchStart(e) {
      if (this.isAnimating) return;
      this.justTouched = true;
      const touch = e.touches[0];
      this.touchStartX = touch.clientX;
      this.touchStartY = touch.clientY;
      this.touchCurrentX = touch.clientX;
      this.isSwiping = false;
      this.swipeOffset = 0;
    },
    onTouchMove(e) {
      if (this.isAnimating) return;
      const touch = e.touches[0];
      const deltaX = touch.clientX - this.touchStartX;
      const deltaY = touch.clientY - this.touchStartY;

      // 判断是否为水平滑动（水平位移大于垂直位移）
      if (!this.isSwiping && Math.abs(deltaX) > 10) {
        if (Math.abs(deltaX) > Math.abs(deltaY)) {
          this.isSwiping = true;
        }
      }
      if (this.isSwiping) {
        e.preventDefault();
        this.touchCurrentX = touch.clientX;

        // 计算滑动偏移，添加边界阻尼
        let offset = deltaX;

        // 第一张往右滑 或 最后一张往左滑，添加阻尼效果
        if (this.previewIndex === 0 && offset > 0 || this.previewIndex === this.mediaFiles.length - 1 && offset < 0) {
          offset = offset * 0.3; // 阻尼系数
        }
        this.swipeOffset = offset;
      }
    },
    onTouchEnd() {
      if (!this.isSwiping) return;
      const deltaX = this.touchCurrentX - this.touchStartX;
      const threshold = window.innerWidth * 0.2; // 滑动超过20%屏幕宽度触发切换
      const velocity = Math.abs(deltaX) / 100; // 简单的速度判断

      this.isSwiping = false;
      this.isAnimating = true;

      // 判断是否切换
      if (deltaX < -threshold || deltaX < -50 && velocity > 0.5) {
        // 左滑 -> 下一张
        if (this.previewIndex < this.mediaFiles.length - 1) {
          this.previewIndex++;
        }
      } else if (deltaX > threshold || deltaX > 50 && velocity > 0.5) {
        // 右滑 -> 上一张
        if (this.previewIndex > 0) {
          this.previewIndex--;
        }
      }

      // 重置偏移
      this.swipeOffset = 0;

      // 动画结束后重置状态
      setTimeout(() => {
        this.isAnimating = false;
        this.justTouched = false;
      }, 350);
    }
  }
};